import React from "react";
import PropTypes from "prop-types";
import checkboxes from "./checkboxesData";
import Checkbox from "./checkboxTemplate";
var ReactDOM = require("react-dom");
var urlArr = [];

export default class Settings extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      IPAddress : '',
      Twilionumber: '',
      phoneNumber:'',
      AuthID:'',
      authToken:''


    };
  }
  componentDidMount() {
    fetch('http://localhost:8080/api/settings')
    .then(response => response.json())

.then(user => {
    console.log(user.IPAddress);
this.setState({
    IPAddress : user.IPAddress,
    Twilionumber: user.Twilionumber,
    phoneNumber: user.phoneNumber,
    AuthID: user.AuthID,
    authToken: user.authToken


  })
});


  }

  handleInputChange = (event) => {
    const { value, name } = event.target;
    this.setState({
      [name]: value
    });
  }

  onSubmit = (event) => {
    console.log(this.state);
    event.preventDefault();
    fetch('http://localhost:8080/api/settings', {
      method: 'PUT',
      body: JSON.stringify(this.state),
      headers: {
        'Content-Type': 'application/json',
        credentials: 'include'

      }
    })
  }


  render() {
    return (
      <form onSubmit={this.onSubmit}>
        <h1>Login Below!</h1>
        <input
          type="IPAddress"
          name="IPAddress"
          placeholder="Enter IPAddress"
          value={this.state.IPAddress}
          onChange={this.handleInputChange}
          required
        />
        <br/>
        <input
          type="Twilionumber"
          name="Twilionumber"
          placeholder="Enter Twilio Number"
          value={this.state.Twilionumber}
          onChange={this.handleInputChange}
          required
        />
          <br/>
        <input
          type="phoneNumber"
          name="phoneNumber"
          placeholder="Enter Phone Number"
          value={this.state.phoneNumber}
          onChange={this.handleInputChange}
          required
        />
          <br/>
        <input
          type="AuthID"
          name="AuthID"
          placeholder="Enter AuthID"
          value={this.state.AuthID}
          onChange={this.handleInputChange}
          required
        />
          <br/>
        <input
          type="authToken"
          name="authToken"
          placeholder="Enter authToken"
          value={this.state.authToken}
          onChange={this.handleInputChange}
          required
        />
  <br/>
        <input type="submit" value="Submit"/>
      </form>
    );
  }
}
